

.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  margin-bottom: 100px;
  button {
    position: absolute;
    top: 50%;
    right: 10%;
    transform: translate(0, -50%);
  }
  input {
    width: 100%;
    display: block;
    padding: 20px;
  }
}